import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import { AddChemistManuallyComponent } from './add-chemist-manually.component';

const routes: Routes = [
  {
    path: '',
    component: AddChemistManuallyComponent,
    data: {
      title: 'Default',
      icon: 'icon-home',
      caption: 'lorem ipsum dolor sit amet, consectetur adipisicing elit',
      status: false
    }
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AddChemistManuallyRoutingModule { }



